import axios from 'axios';

const config = require('./config');

const baseConfig = {
  // `method` 
  method: 'get', //  get

  // `baseURL`  `url`  `url`  URL
  //  `baseURL`  axios  URL
  baseURL: config.baseURL,

  // `transformRequest` 
  //  'PUT', 'POST'  'PATCH' 
  //  ArrayBuffer Stream
  transformRequest: [function (data) {
    //  data 

    return JSON.stringify(data);
  }],

  // `transformResponse`  then/catch 
  transformResponse: [function (data) {
    //  data 
    return data;
  }],

  // `headers` 
  headers: {
    'X-Requested-With': 'XMLHttpRequest',
    'Content-Type': 'application/json; charset=utf-8'
  },

  // `timeout` (0 )
  //  `timeout` 
  timeout: 2000,

  // `withCredentials` 
  withCredentials: false, // 

  // `responseType`  'arraybuffer', 'blob', 'document', 'json', 'text', 'stream'
  responseType: 'json', // 

  // `xsrfCookieName`  xsrf token cookie
  xsrfCookieName: 'XSRF-TOKEN', // default

  // `xsrfHeaderName`  xsrf token  HTTP 
  xsrfHeaderName: 'X-XSRF-TOKEN', // 

  // `maxContentLength` 
  maxContentLength: 2000,

  // `validateStatus` HTTP  resolve  reject  promise  `validateStatus`  `true` ( `null`  `undefined`)promise  resolve; promise  rejecte
  validateStatus: function (status) {
    return status >= 200 && status < 400;
  }
};

// 
axios.interceptors.request.use(function (config) {
  // 
  return config;
}, function (error) {
  // 
  return Promise.reject(error);
});

// 
axios.interceptors.response.use(function (response) {
  // 
  return response;
}, function (error) {
  // 
  return Promise.reject(error);
});

for (let p in baseConfig) {
  axios.defaults[p] = baseConfig[p];
}



